<template>
  <div class="homepage">
    <!-- Navigation Bar -->
    <nav class="navbar">
      <div class="navbar-content">
        <h1 class="navbar-title">Junior High Sociogram</h1>
        <ul class="navbar-links">
          <li><a href="#">About</a></li>
          <li><a href="#">Graphs</a></li>
        </ul>
      </div>
    </nav>

    <!-- File Upload Section -->
    <section class="file-upload">
      <h2>File Upload</h2>
      <p>Upload the game data to get started</p>
      <input
        type="file"
        ref="fileInput"
        @change="handleFileUpload"
        style="display: none"
      />
      <button class="upload-button" @click="triggerFileInput">
        Upload File
      </button>
      <ul v-if="uploadedFiles.length > 0">
        <li v-for="file in uploadedFiles" :key="file">{{ file }}</li>
      </ul>
    </section>
  </div>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      selectedFile: null,
      uploadedFiles: [],
    };
  },
  methods: {
    triggerFileInput() {
      console.log("File input triggered");
      this.$refs.fileInput.click();
    },
    handleFileUpload(event) {
      console.log("File selected:", event.target.files[0]);
      this.selectedFile = event.target.files[0];
      this.uploadFile(); // Call uploadFile after selecting the file
    },
    async uploadFile() {
      console.log("Upload file method called"); // Add this line

      if (!this.selectedFile) {
        alert("Please select a file first");
        return;
      }

      const formData = new FormData();
      formData.append("file", this.selectedFile);

      try {
        const response = await fetch("http://localhost:5000/upload", {
          method: "POST",
          body: formData,
        });

        console.log("Response received:", response); // Add this line

        if (response.ok) {
          const data = await response.json();
          this.uploadedFiles = data.files;
        } else {
          const error = await response.json();
          console.log("Error response from server:", error);
          alert(error.error);
        }
      } catch (error) {
        console.error("Error uploading file:", error);
        alert("Error uploading file");
      }
    },
  },
};
</script>

